ệu vị trí: Dịch vụ của chúng tôi có thể sử dụng các công nghệ hỗ trợ định vị như Google Maps. Nếu bạn sử dụng các dịch vụ này, Google có thể nhận thông tin về vị trí thực của bạn (ví dụ như tín hiệu GPS được gửi qua thiết bị di động) hoặc thông tin có thể được sử dụng để xác định vị trí (như ID điện thoại di động).")]),_vm._v(" "),_c('p',[_vm._v("3. Dữ liệu Nhân khẩu học: những thông tin không phải là thông tin cá nhân, bao gồm nhưng không giới hạn ở mã vùng, quê hương, giới tính, tuổi, ngày tháng năm sinh, lịch sử duyệt web, lịch sử tìm kiếm thông tin.")]),_vm._v(" "),_c('p',[_vm._v("Nếu bạn không thể cung cấp cho chúng tôi thông tin cá nhân cần thiết, bạn có thể không sử dụng được một số phần của các dịch vụ trực tuyến theo yêu cầu.")]),_vm._v(" "),_c('p',[_c('strong',[_vm._v("MỤC ĐÍCH SỬ DỤNG THÔNG TIN KHÁCH HÀNG ")])]),_vm._v(" "),_c('p',[_vm._v("Chúng tôi sử dụng địa chỉ email của bạn và các thông tin cá nhân của bạn để giúp chúng tôi vận hành một cách có hiệu quả các trang web và dịch vụ, liên lạc với bạn khi bạn thực hiện các giao dịch đặt chỗ, để chuyển tiếp thông tin chuyến đi từ những người dùng trang web khác đến cho bạn;  và các hoạt động khác trên trang web (ví dụ gửi tin tức quan trọng mà có thể bạn quan tâm. Trong một số trường hợp, chúng tôi có thể gửi cho bạn các hoạt động truyền thông chứa nội dung thương mại, chẳng hạn như các banner quảng cáo và cung cấp đặc biệt dạng giảm giá hoặc khuyến mại).")]),_vm._v(" "),_c('p',[_vm._v("Chúng tôi có thể sử dụng các thông tin về vị trí để tối ưu hóa các hoạt động được thiết kế cho các thiết bị cầm tay thông minh. Các ứng dụng này sẽ làm cho việc đặt chỗ và theo dõi xe đến đón cũng như tìm kiếm hành khách một cách thuận lợi và linh hoạt hơn phương thức qua website.")]),_vm._v(" "),_c('p',[_vm._v("Chúng tôi cam kết rằng chúng tôi sẽ không tiết lộ thông tin cá nhân của bạn cho bất kỳ bên thứ ba nào không liên quan đến dịch vụ hoặc giao dịch này mà chưa được bạn đồng ý.  ")]),_vm._v(" "),_c('p',[_vm._v("Thông tin cá nhân của khách hàng có thể bị chia sẻ với những công ty khác, nhưng chỉ khi cần thiết để đáp ứng các yêu cầu của khách hàng hay cho những mục đích liên quan. Để cung cấp dịch vụ và đủ thông tin đảm bảo cho việc thanh toán bảo mật online hoặc thanh toán tại trung tâm, phục vụ hỗ trợ hệ thống chăm sóc khách hàng, quản lý khách hàng, chăm sóc và nhận ý kiến phản hồi từ phía khách hàng."),_c('br'),_vm._v(" "),_c('br'),_vm._v(" "),_c('strong',[_vm._v("LƯU TRỮ THÔNG TIN")])]),_vm._v(" "),_c('p',[_vm._v("a. Thời gian lưu trữ thông tin: Công ty có hệ thống máy chủ có khả năng lưu trữ thông tin khách hàng tối thiểu là 02 năm và tối đa là 10 năm. Trong quá trình hoạt động, công ty có thể nâng cao khả năng lưu trữ thông tin nếu cần thiết.")]),_vm._v(" "),_c('p',[_vm._v(" b. Địa chỉ của đơn vị thu thập và quản lý thông tin cá nhân: Đơn vị thu thập và quản lý thông tin: Công ty cổ phần truyền thông Sông Sáng. Địa chỉ: 567A Lạc Long Quân, Phường Xuân La, Quận Tây Hồ, Hà Nội.")]),_vm._v(" "),_c('p',[_vm._v("Việc đăng nhập có thể thực hiện trên máy tính, điện thoại, hay các công cụ khác có tính năng truy cập vào website, ứng dụng di động.")]),_vm._v(" "),_c('p',[_c('strong',[_vm._v("CẬP NHẬT HOẶC XÓA THÔNG TIN")])]),_vm._v(" "),_c('p',[_vm._v("Khi bạn sử dụng các dịch vụ của chúng tôi, chúng tôi thật sự cố gắng cung cấp cho bạn quyền truy cập vào thông tin cá nhân của bạn hoặc để chỉnh sửa dữ liệu này nếu chưa chính xác hoặc xoá dữ liệu đó theo yêu cầu của bạn nếu dữ liệu này không được luật pháp yêu cầu giữ lại. Bạn có thể chỉnh sửa hồ sơ của bạn bất cứ lúc nào. Tất cả những gì bạn cần là tên người dùng và mật khẩu của bạn.")]),_vm._v(" "),_c('p',[_c('strong',[_vm._v("CAM KẾT BẢO MẬT THÔNG TIN KHÁCH HÀNG")])]),_vm._v(" "),_c('p',[_vm._v("Chúng tôi chỉ tập hợp lại các thông tin cá nhân trong phạm vi phù hợp và cần thiết cho mục đích thương mại đúng đắn của chúng tôi. Và chúng tôi duy trì các biện pháp thích hợp nhằm bảo đảm tính an toàn, nguyên vẹn, độ chính xác, và tính bảo mật những thông tin mà người sử dụng đã cung cấp. Ngoài ra, chúng tôi cũng có những biện pháp thích hợp để đảm bảo rằng bên thứ ba cũng sử dụng các biện pháp bảo đảm an toàn cho các thông tin mà chúng tôi cung cấp cho họ. Thông tin cá nhân, thông tin riêng của người sử dụng được thu thập, lưu trữ và bảo vệ một các nghiêm túc, chính xác và bảo mật. Doanh nghiệp thiết lập những phần mềm thu thập, lưu trữ thông tin riêng biệt và được thực hiện duy nhất bởi hệ thống máy chủ của doanh nghiệp và hệ thống máy chủ dự phòng. Việc bảo mật các thông tin cá nhân, thông tin riêng của người sử dụng là tuyệt đối và sẽ không bị xuất trình cho bên thứ ba bất kỳ trừ khi có yêu cầu từ phía cơ quan Nhà nước có thẩm quyền. Doanh nghiệp thiết lập hệ thống bảo vệ thông tin cá nhân người sử dụng qua các hình thức: Thiết lập hệ thống tường lửa ngăn ngừa các hình thức tấn công mạng. Đội ngũ kỹ thuật, nhân viên của doanh nghiệp thường xuyên túc trực theo dõi toàn bộ hoạt động của trang mạng. Đảm bảo mọi cuộc tấn công từ các phía đều được phát hiện kịp thời và thực hiện biện pháp ngăn chặn. Các thông tin cá nhân, thông tin riêng của người sử dụng sẽ được lưu trữ theo các bước và thực hiện bảo mật nghiêm ngặt theo các quy định của pháp luật.")]),_vm._v(" "),_c('p',[_c('strong',[_vm._v("THAY ĐỔI TUYÊN BỐ VỀ QUYỀN RIÊNG TƯ/BẢO MẬT")])]),_vm._v(" "),_c('p',[_vm._v("Chúng tôi có thể sửa đổi Tuyên bố về quyền riêng tư/bảo mật này mà không cần thông báo trước. Trang web này sẽ hiển thị nội dung mới nhất. Nếu có sự thay đổi lớn về việc sử dụng thông tin khách hàng, chúng tôi sẽ thông báo và đăng tải những thay đổi đó trên trang web của chúng tôi để bạn có thể biết được những vấn đề liên quan. Chúng tôi đã áp dụng các biện pháp quản lý và điện tử thích hợp nhằm duy trì tính chính xác của thông tin đã thu thập, ngăn chặn đăng nhập trái phép và đảm bảo sử dụng thông tin đúng cách. ")]),_vm._v(" "),_c('p',[_c('strong',[_vm._v("KHÁCH HÀNG TỰ BẢO VỆ")])]),_vm._v(" "),_c('p',[_vm._v("Vui lòng bảo vệ an 