<template>
    <div class="privacy" >
        <article-header :header="header"></article-header>
        <div class="article-container text-justify">
            <p align="center"><strong>CH&Iacute;NH S&Aacute;CH BẢO MẬT TH&Ocirc;NG TIN</strong></p>
            <p>8GO cam kết bảo vệ quyền ri&ecirc;ng tư của dữ liệu c&aacute; nh&acirc;n của bạn v&agrave; hy vọng bạn hiểu r&otilde; c&aacute;c quyền li&ecirc;n quan của m&igrave;nh để bạn c&oacute; thể cảm thấy thoải m&aacute;i khi sử dụng tất cả c&aacute;c dịch vụ Internet li&ecirc;n quan do 8GO cung cấp (sau đ&acirc;y gọi tắt l&agrave; &ldquo;Ch&uacute;ng t&ocirc;i&rdquo;). Ngo&agrave;i ra, để tăng cường bảo vệ th&ocirc;ng tin, bạn phải cung cấp th&ocirc;ng tin nhận dạng c&aacute; nh&acirc;n cho ch&uacute;ng t&ocirc;i để truy cập một số phần nhất định của trang web v&agrave; ứng dụng di động của ch&uacute;ng t&ocirc;i, v&agrave; khi bạn sử dụng c&aacute;c dịch vụ của ch&uacute;ng t&ocirc;i hoặc dịch vụ của c&aacute;c đối t&aacute;c nhưng c&oacute; li&ecirc;n kết với dịch vụ của ch&uacute;ng t&ocirc;i.</p>
            <p><strong>THU THẬP TH&Ocirc;NG TIN KH&Aacute;CH H&Agrave;NG</strong></p>
            <p>Th&ocirc;ng tin của người sử dụng 8GO được thu thập một c&aacute;ch chủ động v&agrave; thụ động. Thu thập th&ocirc;ng tin một c&aacute;ch chủ động l&agrave; khi người sử dụng đăng nhập th&ocirc;ng tin v&agrave;o đơn đặt h&agrave;ng trong qu&aacute; tr&igrave;nh tiến h&agrave;nh mua b&aacute;n trực tuyến. Thu thập th&ocirc;ng tin một c&aacute;ch thụ động l&agrave; khi m&aacute;y chủ tự động ghi ch&eacute;p c&aacute;c dữ liệu li&ecirc;n quan đến trang web bao gồm địa chỉ IP, loại tr&igrave;nh duyệt v&agrave; độ ph&acirc;n giải bằng c&aacute;c chương tr&igrave;nh ph&acirc;n t&iacute;ch h&agrave;nh vi kh&aacute;ch h&agrave;ng nhằm phục vụ cho mục đ&iacute;ch thống k&ecirc;.</p>
            <p><strong>LOẠI TH&Ocirc;NG TIN THU THẬP</strong></p>
            <p>Bằng những phương thức n&ecirc;u tr&ecirc;n, ch&uacute;ng t&ocirc;i c&oacute; thể thu thập th&ocirc;ng tin c&aacute; nh&acirc;n của bạn, bao gồm nhưng kh&ocirc;ng giới hạn:&nbsp;</p>
            <p>1. Dữ liệu c&aacute; nh&acirc;n như: họ t&ecirc;n, th&ocirc;ng tin t&agrave;i khoản ng&acirc;n h&agrave;ng/thẻ t&iacute;n dụng / thẻ ghi nợ; Th&ocirc;ng tin li&ecirc;n lạc như số điện thoại, địa chỉ gửi thư, địa chỉ email, số fax; Th&ocirc;ng tin c&aacute; nh&acirc;n được tự động tạo ra khi th&agrave;nh vi&ecirc;n sử dụng website v&agrave; ứng dụng di động như: th&ocirc;ng tin IP, cookies, đăng nhập website.</p>
            <p>2. Dữ liệu vị tr&iacute;: Dịch vụ của ch&uacute;ng t&ocirc;i c&oacute; thể sử dụng c&aacute;c c&ocirc;ng nghệ hỗ trợ định vị như Google Maps. Nếu bạn sử dụng c&aacute;c dịch vụ n&agrave;y, Google c&oacute; thể nhận th&ocirc;ng tin về vị tr&iacute; thực của bạn (v&iacute; dụ như t&iacute;n hiệu GPS được gửi qua thiết bị di động) hoặc th&ocirc;ng tin c&oacute; thể được sử dụng để x&aacute;c định vị tr&iacute; (như ID điện thoại di động).</p>
            <p>3. Dữ liệu Nh&acirc;n khẩu học: những th&ocirc;ng tin kh&ocirc;ng phải l&agrave; th&ocirc;ng tin c&aacute; nh&acirc;n, bao gồm nhưng kh&ocirc;ng giới hạn ở m&atilde; v&ugrave;ng, qu&ecirc; hương, giới t&iacute;nh, tuổi, ng&agrave;y th&aacute;ng năm sinh, lịch sử duyệt web, lịch sử t&igrave;m kiếm th&ocirc;ng tin.</p>

            <p>Nếu bạn kh&ocirc;ng thể cung cấp cho ch&uacute;ng t&ocirc;i th&ocirc;ng tin c&aacute; nh&acirc;n cần thiết, bạn c&oacute; thể kh&ocirc;ng sử dụng được một số phần của c&aacute;c dịch vụ trực tuyến theo y&ecirc;u cầu.</p>
            <p><strong>MỤC Đ&Iacute;CH SỬ DỤNG TH&Ocirc;NG TIN KH&Aacute;CH H&Agrave;NG </strong></p>
            <p>Ch&uacute;ng t&ocirc;i sử dụng địa chỉ email của bạn v&agrave; c&aacute;c th&ocirc;ng tin c&aacute; nh&acirc;n của bạn để gi&uacute;p ch&uacute;ng t&ocirc;i vận h&agrave;nh một c&aacute;ch c&oacute; hiệu quả c&aacute;c trang web v&agrave; dịch vụ, li&ecirc;n lạc với bạn khi bạn thực hiện c&aacute;c giao dịch đặt ch&ocirc;̃, để chuyển tiếp th&ocirc;ng tin chuyến đi từ những người d&ugrave;ng trang web kh&aacute;c đến cho bạn;&nbsp; v&agrave; c&aacute;c hoạt động kh&aacute;c tr&ecirc;n trang web (v&iacute; dụ gửi tin tức quan trọng m&agrave; c&oacute; thể bạn quan t&acirc;m. Trong một số trường hợp, ch&uacute;ng t&ocirc;i c&oacute; thể gửi cho bạn c&aacute;c hoạt động truyền th&ocirc;ng chứa nội dung thương mại, chẳng hạn như c&aacute;c banner quảng c&aacute;o v&agrave; cung cấp đặc biệt dạng giảm gi&aacute; hoặc khuyến mại).</p>
            <p>Ch&uacute;ng t&ocirc;i c&oacute; thể sử dụng c&aacute;c th&ocirc;ng tin về vị tr&iacute; để tối ưu h&oacute;a c&aacute;c hoạt đ&ocirc;̣ng được thiết kế cho c&aacute;c thiết bị cầm tay th&ocirc;ng minh. C&aacute;c ứng dụng n&agrave;y sẽ l&agrave;m cho vi&ecirc;̣c đặt ch&ocirc;̃ v&agrave; theo d&otilde;i xe đ&ecirc;́n đ&oacute;n cũng như t&igrave;m ki&ecirc;́m h&agrave;nh kh&aacute;ch một c&aacute;ch thuận lợi v&agrave; linh hoạt hơn phương thức qua website.</p>
            <p>Ch&uacute;ng t&ocirc;i cam kết rằng ch&uacute;ng t&ocirc;i sẽ kh&ocirc;ng tiết lộ th&ocirc;ng tin c&aacute; nh&acirc;n của bạn cho bất kỳ b&ecirc;n thứ ba n&agrave;o kh&ocirc;ng li&ecirc;n quan đến dịch vụ hoặc giao dịch n&agrave;y m&agrave; chưa được bạn đồng &yacute;.&nbsp;&nbsp;</p>
            <p>Th&ocirc;ng tin c&aacute; nh&acirc;n của kh&aacute;ch h&agrave;ng c&oacute; thể bị chia sẻ với những c&ocirc;ng ty kh&aacute;c, nhưng chỉ khi cần thiết để đ&aacute;p ứng c&aacute;c y&ecirc;u cầu của kh&aacute;ch h&agrave;ng hay cho những mục đ&iacute;ch li&ecirc;n quan. Để cung cấp dịch vụ v&agrave; đủ th&ocirc;ng tin đảm bảo cho việc thanh to&aacute;n bảo mật online hoặc thanh to&aacute;n tại trung t&acirc;m, phục vụ hỗ trợ hệ thống chăm s&oacute;c kh&aacute;ch h&agrave;ng, quản l&yacute; kh&aacute;ch h&agrave;ng, chăm s&oacute;c v&agrave; nhận &yacute; kiến phản hồi từ ph&iacute;a kh&aacute;ch h&agrave;ng.<br /> <br /> <strong>LƯU TRỮ TH&Ocirc;NG TIN</strong></p>
            <p>a. Thời gian lưu trữ th&ocirc;ng tin: C&ocirc;ng ty c&oacute; hệ thống m&aacute;y chủ c&oacute; khả năng lưu trữ th&ocirc;ng tin kh&aacute;ch h&agrave;ng tối thiểu l&agrave; 02 năm v&agrave; tối đa l&agrave; 10 năm. Trong qu&aacute; tr&igrave;nh hoạt động, c&ocirc;ng ty c&oacute; thể n&acirc;ng cao khả năng lưu trữ th&ocirc;ng tin nếu cần thiết.</p>
            <p>&nbsp;b. Địa chỉ của đơn vị thu thập v&agrave; quản l&yacute; th&ocirc;ng tin c&aacute; nh&acirc;n: Đơn vị thu thập v&agrave; quản l&yacute; th&ocirc;ng tin: C&ocirc;ng ty cổ phần truyền th&ocirc;ng S&ocirc;ng S&aacute;ng. Địa chỉ: 567A Lạc Long Qu&acirc;n, Phường Xu&acirc;n La, Quận T&acirc;y Hồ, H&agrave; Nội.</p>
            <p>Việc đăng nhập c&oacute; thể thực hiện tr&ecirc;n m&aacute;y t&iacute;nh, điện thoại, hay c&aacute;c c&ocirc;ng cụ kh&aacute;c c&oacute; t&iacute;nh năng truy cập v&agrave;o website, ứng dụng di động.</p>
            <p><strong>CẬP NHẬT HOẶC X&Oacute;A TH&Ocirc;NG TIN</strong></p>
            <p>Khi bạn sử dụng c&aacute;c dịch vụ của ch&uacute;ng t&ocirc;i, ch&uacute;ng t&ocirc;i thật sự cố gắng cung cấp cho bạn quyền truy cập v&agrave;o th&ocirc;ng tin c&aacute; nh&acirc;n của bạn hoặc để chỉnh sửa dữ liệu n&agrave;y nếu chưa ch&iacute;nh x&aacute;c hoặc xo&aacute; dữ liệu đ&oacute; theo y&ecirc;u cầu của bạn nếu dữ liệu n&agrave;y kh&ocirc;ng được luật ph&aacute;p y&ecirc;u cầu giữ lại. Bạn c&oacute; thể chỉnh sửa hồ sơ của bạn bất cứ l&uacute;c n&agrave;o. Tất cả những g&igrave; bạn cần l&agrave; t&ecirc;n người d&ugrave;ng v&agrave; mật khẩu của bạn.</p>
            <p><strong>CAM KẾT BẢO MẬT TH&Ocirc;NG TIN KH&Aacute;CH H&Agrave;NG</strong></p>
            <p>Ch&uacute;ng t&ocirc;i chỉ tập hợp lại c&aacute;c th&ocirc;ng tin c&aacute; nh&acirc;n trong phạm vi ph&ugrave; hợp v&agrave; cần thiết cho mục đ&iacute;ch thương mại đ&uacute;ng đắn của ch&uacute;ng t&ocirc;i. V&agrave; ch&uacute;ng t&ocirc;i duy tr&igrave; c&aacute;c biện ph&aacute;p th&iacute;ch hợp nhằm bảo đảm t&iacute;nh an to&agrave;n, nguy&ecirc;n vẹn, độ ch&iacute;nh x&aacute;c, v&agrave; t&iacute;nh bảo mật những th&ocirc;ng tin m&agrave; người sử dụng đ&atilde; cung cấp. Ngo&agrave;i ra, ch&uacute;ng t&ocirc;i cũng c&oacute; những biện ph&aacute;p th&iacute;ch hợp để đảm bảo rằng b&ecirc;n thứ ba cũng sử dụng c&aacute;c biện ph&aacute;p bảo đảm an to&agrave;n cho c&aacute;c th&ocirc;ng tin m&agrave; ch&uacute;ng t&ocirc;i cung cấp cho họ. Th&ocirc;ng tin c&aacute; nh&acirc;n, th&ocirc;ng tin ri&ecirc;ng của người sử dụng được thu thập, lưu trữ v&agrave; bảo vệ một c&aacute;c nghi&ecirc;m t&uacute;c, ch&iacute;nh x&aacute;c v&agrave; bảo mật. Doanh nghiệp thiết lập những phần mềm thu thập, lưu trữ th&ocirc;ng tin ri&ecirc;ng biệt v&agrave; được thực hiện duy nhất bởi hệ thống m&aacute;y chủ của doanh nghiệp v&agrave; hệ thống m&aacute;y chủ dự ph&ograve;ng. Việc bảo mật c&aacute;c th&ocirc;ng tin c&aacute; nh&acirc;n, th&ocirc;ng tin ri&ecirc;ng của người sử dụng l&agrave; tuyệt đối v&agrave; sẽ kh&ocirc;ng bị xuất tr&igrave;nh cho b&ecirc;n thứ ba bất kỳ trừ khi c&oacute; y&ecirc;u cầu từ ph&iacute;a cơ quan Nh&agrave; nước c&oacute; thẩm quyền. Doanh nghiệp thiết lập hệ thống bảo vệ th&ocirc;ng tin c&aacute; nh&acirc;n người sử dụng qua c&aacute;c h&igrave;nh thức: Thiết lập hệ thống tường lửa ngăn ngừa c&aacute;c h&igrave;nh thức tấn c&ocirc;ng mạng. Đội ngũ kỹ thuật, nh&acirc;n vi&ecirc;n của doanh nghiệp thường xuy&ecirc;n t&uacute;c trực theo d&otilde;i to&agrave;n bộ hoạt động của trang mạng. Đảm bảo mọi cuộc tấn c&ocirc;ng từ c&aacute;c ph&iacute;a đều được ph&aacute;t hiện kịp thời v&agrave; thực hiện biện ph&aacute;p ngăn chặn. C&aacute;c th&ocirc;ng tin c&aacute; nh&acirc;n, th&ocirc;ng tin ri&ecirc;ng của người sử dụng sẽ được lưu trữ theo c&aacute;c bước v&agrave; thực hiện bảo mật nghi&ecirc;m ngặt theo c&aacute;c quy định của ph&aacute;p luật.</p>
            <p><strong>THAY ĐỔI TUY&Ecirc;N BỐ VỀ QUYỀN RI&Ecirc;NG TƯ/BẢO MẬT</strong></p>
            <p>Ch&uacute;ng t&ocirc;i c&oacute; thể sửa đổi Tuy&ecirc;n bố về quyền ri&ecirc;ng tư/bảo mật n&agrave;y m&agrave; kh&ocirc;ng cần th&ocirc;ng b&aacute;o trước. Trang web n&agrave;y sẽ hiển thị nội dung mới nhất. Nếu c&oacute; sự thay đổi lớn về việc sử dụng th&ocirc;ng tin kh&aacute;ch h&agrave;ng, ch&uacute;ng t&ocirc;i sẽ th&ocirc;ng b&aacute;o v&agrave; đăng tải những thay đổi đ&oacute; tr&ecirc;n trang web của ch&uacute;ng t&ocirc;i để bạn c&oacute; thể biết được những vấn đề li&ecirc;n quan. Ch&uacute;ng t&ocirc;i đ&atilde; &aacute;p dụng c&aacute;c biện ph&aacute;p quản l&yacute; v&agrave; điện tử th&iacute;ch hợp nhằm duy tr&igrave; t&iacute;nh ch&iacute;nh x&aacute;c của th&ocirc;ng tin đ&atilde; thu thập, ngăn chặn đăng nhập tr&aacute;i ph&eacute;p v&agrave; đảm bảo sử dụng th&ocirc;ng tin đ&uacute;ng c&aacute;ch.&nbsp;</p>
            <p><strong>KH&Aacute;CH H&Agrave;NG TỰ BẢO VỆ</strong></p>
            <p>Vui l&ograve;ng bảo vệ an to&agrave;n th&ocirc;ng tin c&aacute; nh&acirc;n của bạn v&agrave; kh&ocirc;ng n&ecirc;n cung cấp bất kỳ th&ocirc;ng tin c&aacute; nh&acirc;n n&agrave;o cho người kh&aacute;c. Kh&ocirc;ng n&ecirc;n qu&ecirc;n đăng xuất sau khi ho&agrave;n tất giao dịch&nbsp; v&agrave; thay đổi th&ocirc;ng tin li&ecirc;n hệ của th&agrave;nh vi&ecirc;n. Nếu bạn d&ugrave;ng chung m&aacute;y t&iacute;nh, điện thoại di động với người kh&aacute;c hoặc sử dụng m&aacute;y t&iacute;nh c&ocirc;ng cộng, vui l&ograve;ng đ&oacute;ng cửa sổ tr&igrave;nh duyệt để bảo vệ th&ocirc;ng tin c&aacute; nh&acirc;n kh&ocirc;ng bị thu thập v&agrave; sử dụng tr&aacute;i ph&eacute;p.</p>
            <p>&nbsp;</p>
        </div>
    </div>
</template>

